<template>
	<div class="NavbarSidebarSidebar">
		<Layout>	
			<Content :style="{background: '#fff', minHeight: '500px'}">
				<Layout>
					<!-- 导航栏 -->
					<Sider hide-trigger :style="{background: '#fff'}">
						<Menu :active-name="activeName" width="auto">
							<MenuItem :name="item.id" ref="menus" v-for="(item,index) in list" :key="index"
								:to="item.id">
							{{item.name}}
							</MenuItem>
						</Menu>
					</Sider>
					<Layout :style="{padding: '24px'}">
						<!-- 内容 -->
						<!-- <Breadcrumb :style="{margin: '16px 0'}"></Breadcrumb> -->
						<Content class="contentItem">
							<Card class="CardItem" dis-hover>
								<router-view />
							</Card>
						</Content>
					</Layout>
				</Layout>
			</Content>
		</Layout>
	</div>
</template>

<script>
	export default {
		name: "NavbarSidebar",
		data() {
			return {
				list: [],
				headTopType: localStorage.getItem('headTopType'),
				activeName: this.$route.path,
			};
		},
		watch: {
			'$route'(to) {
				// 对路由变化作出响应...
				// console.log('路由变化了',from);
				console.log('to', to);
				this.headTopType = localStorage.getItem('headTopType')
				this.handList()
				this.$nextTick(() => {
					// DOM 更新了
					this.activeName = to.path
				})
			},
		},
		mounted() {
			this.handList()
		},
		methods: {
			handList() {
				this.list = []
				if (this.headTopType == "home") {
					this.list = [{
							id: '/BasicSetuUp',
							name: '基本设置'
						},
						{
							id: '/CustomerCapital',
							name: '客资管理'
						},
						{
							id: '/FansManagement',
							name: '粉丝管理'
						},
						{
							id: '/DialogSettings',
							name: '对话设置'
						},
						{
							id: '/DataAnalysis',
							name: '数据分析'
						},
					]
				}
				if (this.headTopType == "Sidebar") {
					this.list = [{
							id: '/EnterpriseInfo',
							name: '企业信息'
						},
						{
							id: '/StaffManagement',
							name: '员工管理'
						}
					]
				}
			},


			toItem(i) {
				console.log(i)
			}
		}
	};
</script>


<style scoped>
	.ivu-layout {
		height: 100%;
		/* overflow-y: hidden; */
	}
	.ivu-card-body{
		height: 100%;
	}
	.ivu-layout.ivu-layout-has-sider>.ivu-layout, .ivu-layout.ivu-layout-has-sider>.ivu-layout-content{
		    overflow-x: auto;

	}
	.ivu-layout-header {
		padding: 0;
		line-height: 1.5;
		font-size: 14px;
		height: 60px;
	}
	.contentItem /deep/ .ivu-card-body{
		padding: 0px;
	}
	.ivu-menu-vertical.ivu-menu-light:after{
		width: 0px;
	}
	.ivu-menu-light{
		text-align: center;
	}
/* 	.ivu-menu-light.ivu-menu-vertical .ivu-menu-item-active:not(.ivu-menu-submenu):after{
		width: 0px;
	} */
</style>
 <!-- :style="{}" -->
<style scoped lang="less">
	.NavbarSidebarSidebar {
		height: 100%;
		width: 100%;
		min-width: 1540px;
		overflow-y: hidden;
		background: #F9F9F9;
		.contentItem{
			.CardItem{
				// min-height: 800px;
				height: 100%;
			}
		}
	}
</style>
